






























import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class HelloWorld extends Vue {
  @Prop() private msg!: string;
  todos = [
    {
      id: 0,
      text: "vue-router",
      done: false,
      editing: false
    },
    {
      id: 1,
      text: "vuex",
      done: false,
      editing: false
    },
    {
      id: 2,
      text: "vue-loader",
      done: false,
      editing: false
    },
    {
      id: 3,
      text: "awesome-vue",
      done: true,
      editing: false
    }
  ];
  newTodo = "";
  addTodo(): void {
    const text = this.newTodo && this.newTodo.trim();
    if (!text) {
      return;
    }
    this.todos.push({
      id: this.getMaxId() + 1,
      text: text,
      done: false,
      editing: false
    });
    this.newTodo = "";
  }
  removeTodo(): void {
    for (let i = this.todos.length - 1; i >= 0; i--) {
      if (this.todos[i].done) {
        this.todos.splice(i, 1);
      }
    }
  }
  getMaxId(): number {
    return this.todos.map(t => t.id).reduce((a, b) => Math.max(a, b));
  }
}
